        padding-right: ~"calc(@{value} + @{offset_one_side})";
    }
}
.menu(@name, @value){
    @media screen and (max-width: @break_menu){
        @{name}: @value;
    }
}
.menu-block(@content){
    @media screen and (max-width: @break_menu){
        @content();
    }
}
.debug(@background, @outline){
    height: 100vh;
    position: fixed;
    width: 100%;
    
    > div{
        height: inherit;
        .wrapper();
        
        > div{
            height: inherit;
            .row-flex();
            
            > div{
                .col();
                .size(1);
                height: inherit;
                outline: @outline;
                background: @background;
            }
        }
    }
}
.clearfix(){
    &::after{
        display: block;
        content: "";
        clear: both;
    }
}
